.popup-div {
    display: none;
    border: 1px solid rgba(0,0,0,0.2);
    border-radius: 2px;
    background: #fff;
    z-index: 99;
    width: 100% !important;
    position: absolute;
    width: 300px;
    -webkit-border-radius: 2px;
    -moz-border-radius: 2px;
    -ms-border-radius: 2px;
    -o-border-radius: 2px;
    top: 50px;
    left: 0;
    overflow-x: hidden;
}
