}
.delta {
    position: relative;
    border-radius: 10px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
}
.delta:after {
    content: '';
    position: absolute;
    left: 20px;
    bottom: 0px;
    border: 10px solid transparent;
    border-bottom: 10px solid #f0f0f0;
}
.delta:before {
    content: '';
    position: absolute;
    left: 20px;
    bottom: 20px;
}
.cartL {
    top: 2px !important;
